<template>
  <div class="pdf-convert">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>PDF格式转换</span>
          <el-icon><Refresh /></el-icon>
        </div>
      </template>
      
      <el-form :model="form" label-width="120px">
        <el-form-item label="选择PDF文件" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :limit="1"
            :on-change="handleFileChange"
            :before-upload="beforeUpload"
            accept=".pdf"
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              将PDF文件拖到此处，或<em>点击上传</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                只能上传PDF文件，且不超过100MB
              </div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="转换类型" required>
          <el-radio-group v-model="form.convertType" @change="handleConvertTypeChange">
            <el-radio label="word">转Word</el-radio>
            <el-radio label="excel">转Excel</el-radio>
            <el-radio label="ppt">转PPT</el-radio>
            <el-radio label="image">转图片</el-radio>
            <el-radio label="txt">转TXT</el-radio>
            <el-radio label="html">转HTML</el-radio>
            <el-radio label="markdown">转Markdown</el-radio>
          </el-radio-group>
        </el-form-item>

        <!-- Word转换选项 -->
        <template v-if="form.convertType === 'word'">
          <el-form-item label="输出格式">
            <el-radio-group v-model="form.outputFormat">
              <el-radio label="docx">DOCX</el-radio>
              <el-radio label="doc">DOC</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="保持格式">
            <el-switch v-model="form.preserveFormatting" />
            <div class="form-tip">是否保持原PDF的格式</div>
          </el-form-item>
        </template>

        <!-- Excel转换选项 -->
        <template v-if="form.convertType === 'excel'">
          <el-form-item label="输出格式">
            <el-radio-group v-model="form.outputFormat">
              <el-radio label="xlsx">XLSX</el-radio>
              <el-radio label="xls">XLS</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="只提取表格">
            <el-switch v-model="form.extractTables" />
            <div class="form-tip">是否只提取PDF中的表格内容</div>
          </el-form-item>
        </template>

        <!-- PPT转换选项 -->
        <template v-if="form.convertType === 'ppt'">
          <el-form-item label="输出格式">
            <el-radio-group v-model="form.outputFormat">
              <el-radio label="pptx">PPTX</el-radio>
              <el-radio label="ppt">PPT</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="每页一幻灯片">
            <el-switch v-model="form.onePagePerSlide" />
            <div class="form-tip">是否将PDF的每一页转换为一个幻灯片</div>
          </el-form-item>
        </template>

        <!-- 图片转换选项 -->
        <template v-if="form.convertType === 'image'">
          <el-form-item label="图片格式">
            <el-radio-group v-model="form.imageFormat">
              <el-radio label="png">PNG</el-radio>
              <el-radio label="jpg">JPG</el-radio>
              <el-radio label="jpeg">JPEG</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="分辨率(DPI)">
            <el-input-number
              v-model="form.dpi"
              :min="72"
              :max="600"
              :step="72"
            />
          </el-form-item>
          <el-form-item label="页数范围">
            <el-input
              v-model="form.pageRange"
              placeholder="如: 1-5 (不填则转换全部)"
              clearable
            />
            <div class="form-tip">指定要转换的页数范围，不填则转换全部页面</div>
          </el-form-item>
        </template>

        <!-- TXT转换选项 -->
        <template v-if="form.convertType === 'txt'">
          <el-form-item label="文本编码">
            <el-select v-model="form.encoding" placeholder="选择编码">
              <el-option label="UTF-8" value="utf-8" />
              <el-option label="GBK" value="gbk" />
              <el-option label="GB2312" value="gb2312" />
              <el-option label="ISO-8859-1" value="iso-8859-1" />
            </el-select>
          </el-form-item>
          <el-form-item label="页数范围">
            <el-input
              v-model="form.pageRange"
              placeholder="如: 1-5 (不填则转换全部)"
              clearable
            />
          </el-form-item>
        </template>

        <!-- HTML转换选项 -->
        <template v-if="form.convertType === 'html'">
          <el-form-item label="按页分节">
            <el-switch v-model="form.splitByPage" />
          </el-form-item>
          <el-form-item label="内嵌图片">
            <el-switch v-model="form.embedImages" />
          </el-form-item>
          <el-form-item label="页数范围">
            <el-input
              v-model="form.pageRange"
              placeholder="如: 1-5 (不填则转换全部)"
              clearable
            />
          </el-form-item>
        </template>

        <!-- Markdown转换选项 -->
        <template v-if="form.convertType === 'markdown'">
          <el-form-item label="标题识别">
            <el-select v-model="form.headingDetection">
              <el-option label="自动" value="auto" />
              <el-option label="简单" value="simple" />
              <el-option label="关闭" value="none" />
            </el-select>
          </el-form-item>
          <el-form-item label="包含图片">
            <el-switch v-model="form.includeImages" />
          </el-form-item>
          <el-form-item label="页数范围">
            <el-input
              v-model="form.pageRange"
              placeholder="如: 1-5 (不填则转换全部)"
              clearable
            />
          </el-form-item>
        </template>

        <el-form-item>
          <el-button
            type="primary"
            :loading="loading"
            :disabled="!selectedFile"
            @click="handleSubmit"
          >
            {{ loading ? '转换中...' : '开始转换' }}
          </el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>

      <!-- 结果显示 -->
      <div v-if="result" class="result-section">
        <el-divider>转换结果</el-divider>
        <el-alert
          :title="result.msg"
          type="success"
          :closable="false"
          show-icon
        />
        <div class="result-info">
          <p><strong>任务ID:</strong> {{ result.data.taskId }}</p>
          <p v-if="form.convertType === 'image'">
            <strong>转换图片数:</strong> {{ result.data.images?.length || 0 }}
          </p>
          <p v-else>
            <strong>转换文件:</strong> {{ result.data.convertedFile }}
          </p>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            下载结果
          </el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Refresh, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { validateFile, FILE_TYPES } from '@/utils/file'
import { downloadFile } from '@/utils/download'

const uploadRef = ref()
const loading = ref(false)
const selectedFile = ref(null)
const result = ref(null)

const form = reactive({
  convertType: 'word',
  outputFormat: 'docx',
  preserveFormatting: true,
  extractTables: false,
  onePagePerSlide: true,
  imageFormat: 'png',
  dpi: 300,
  pageRange: ''
  ,
  encoding: 'utf-8',
  splitByPage: true,
  embedImages: true,
  headingDetection: 'auto',
  includeImages: true
})

// 转换类型改变处理
const handleConvertTypeChange = () => {
  // 重置相关选项
  if (form.convertType === 'word') {
    form.outputFormat = 'docx'
  } else if (form.convertType === 'excel') {
    form.outputFormat = 'xlsx'
  } else if (form.convertType === 'ppt') {
    form.outputFormat = 'pptx'
  } else if (form.convertType === 'image') {
    form.imageFormat = 'png'
    form.dpi = 300
    form.pageRange = ''
  } else if (form.convertType === 'txt') {
    form.encoding = 'utf-8'
    form.pageRange = ''
  } else if (form.convertType === 'html') {
    form.splitByPage = true
    form.embedImages = true
    form.pageRange = ''
  } else if (form.convertType === 'markdown') {
    form.headingDetection = 'auto'
    form.includeImages = true
    form.pageRange = ''
  }
}

// 文件选择处理
const handleFileChange = (file) => {
  const validation = validateFile(file.raw, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    uploadRef.value.clearFiles()
    return
  }
  selectedFile.value = file.raw
}

// 上传前验证
const beforeUpload = (file) => {
  const validation = validateFile(file, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // 阻止自动上传
}

// 提交处理
const handleSubmit = async () => {
  if (!selectedFile.value) {
    ElMessage.warning('请先选择PDF文件')
    return
  }

  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', selectedFile.value)

    let response
    switch (form.convertType) {
      case 'word':
        formData.append('outputFormat', form.outputFormat)
        formData.append('preserveFormatting', form.preserveFormatting.toString())
        response = await pdfApi.convertToWord(formData)
        break
      case 'excel':
        formData.append('outputFormat', form.outputFormat)
        formData.append('extractTables', form.extractTables.toString())
        response = await pdfApi.convertToExcel(formData)
        break
      case 'ppt':
        formData.append('outputFormat', form.outputFormat)
        formData.append('onePagePerSlide', form.onePagePerSlide.toString())
        response = await pdfApi.convertToPpt(formData)
        break
      case 'image':
        formData.append('imageFormat', form.imageFormat)
        formData.append('dpi', form.dpi.toString())
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToImages(formData)
        break
      case 'txt':
        formData.append('encoding', form.encoding)
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToTxt(formData)
        break
      case 'html':
        formData.append('splitByPage', form.splitByPage.toString())
        formData.append('embedImages', form.embedImages.toString())
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToHtml(formData)
        break
      case 'markdown':
        formData.append('includeImages', form.includeImages.toString())
        formData.append('headingDetection', form.headingDetection)
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToMarkdown(formData)
        break
    }

    result.value = response
    ElMessage.success('PDF转换成功')
  } catch (error) {
    console.error('PDF转换失败:', error)
  } finally {
    loading.value = false
  }
}

// 下载结果
const downloadResult = async () => {
  if (result.value?.data?.downloadUrl) {
    try {
      let filename
      if (form.convertType === 'image') {
        filename = 'converted_images.zip'
      } else {
        filename = result.value.data.convertedFile || 'converted_file'
      }
      
      // 使用统一的下载工具函数
      downloadFile(result.value.data.downloadUrl, filename)
      
      ElMessage.success('下载已开始')
    } catch (error) {
      console.error('下载失败:', error)
      ElMessage.error('下载失败，请重试')
    }
  } else {
    ElMessage.warning('没有可下载的文件')
  }
}

// 重置表单
const resetForm = () => {
  form.convertType = 'word'
  form.outputFormat = 'docx'
  form.preserveFormatting = true
  form.extractTables = false
  form.onePagePerSlide = true
  form.imageFormat = 'png'
  form.dpi = 300
  form.pageRange = ''
  selectedFile.value = null
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.pdf-convert {
  max-width: 800px;
  margin: 0 auto;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 5px;
}

.result-section {
  margin-top: 20px;
}

.result-info {
  margin-top: 15px;
  padding: 15px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.result-info p {
  margin: 8px 0;
}
</style>
